import React from 'react';

function AboutUs() {

    return (
        <div className="about">
            <div className="aboutwrap">
                <h1>About Us</h1>
                <p>Making these words our foundation we at our Entrepreneurship Cell try to build arks so that budding entrepreneurs can wade through. We play a key role in India's transformation in the new millennium by turning a pile of creative ideas into sustainable innovations. We endeavour to be altruistic in assisting the students to kick off from being  <span>WANTREPRENEURS TO ENTREPRENEURS.</span></p>
            </div>
        </div>
    );
}

export default AboutUs;