import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Route, Link, Switch, useLocation, withRouter } from "react-router-dom";
import Testmonial from "./Testimonial";
import Team from './Team';
import AboutUs from "./AboutUs";
import Footer from "./Footer"

function _ScrollToTop(props) {
    const { pathname } = useLocation();
    useEffect(() => {
        window.scrollTo(0, 0);
    }, [pathname]);
    return props.children
}

const ScrollToTop = withRouter(_ScrollToTop)

function NavBar() {

    const [affix, setAffix] = useState(false);
    const [active, setActive] = useState(false);

    useEffect(()=>{
        window.addEventListener("scroll", ()=>{
            if(window.scrollY > 10){
                setAffix(true);
            }else{
                setAffix(false);
            }
        });
    },[]);

    const toogleClass = () => {
        setActive(!active);
    }

    return (
        <Router>
            <ScrollToTop>
                <nav className={"nav " + (affix ? "affix":"")}>
                    <div className="container">
                        <div className="logo">
                            <Link to="/">Your Logo</Link>
                        </div>
                        <div id="mainListDiv" className={"main_list " + (active ? "show_list": "")}>
                            <ul className="navlinks">
                                <li><Link to="/team">Team</Link></li>
                            </ul>
                        </div>
                        <span className={"navTrigger "+ (active? "active": "")} onClick={toogleClass} >
                            <i></i>
                            <i></i>
                            <i></i>
                        </span>
                    </div>
                </nav>
            </ScrollToTop>
            <Switch>
                <Route exact path="/" component={Home} />
                <Route path="/team" component={Team} />
                <Route component={PageNotFound} />
            </Switch>
        </Router>
    );
}

function Home() {
    return (
        <>
            <section className="home"></section>
            <AboutUs />
            <Testmonial />
            <Footer />
        </>
    );
}

function PageNotFound() {
    return (
        <div>
            404 Error
        </div>
    );
}

export default NavBar;